import instance from './axios.js'

	function getarticle(pageindex,pagesize){
		return instance({
			url:'/article/getarticle',
			method:'post',
			data:{
				pageindex,
				pagesize
			}
			
		})
	}
	
	function upimg(formdata){
		return instance({
			url:'/file/upload',
			method:'post',
			
		// headers:{'Content-Type':'multipart/form-data'},
			data:formdata
	,
			
		})
	}
	
	 function addarticle(articletitle,articleintro,classify,articlecontent,img,span){
		return instance({
			url:'/article/addarticle',
			method:'post',
			data:{
				articletitle,
				articleintro,
				classify,
				articlecontent,
				img,
				span
			}
		})
	}
	
	function deletearticle(_id){
		return instance({
			url:'/article/deletearticle',
			method:'post',
			data:{
				_id
			}
		}) 
			
		
	}
	
	
	export {upimg,addarticle,getarticle,deletearticle}